body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stop-watch {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stop-watch__sec {
  z-index: 1;
  transform: rotate(0, 180deg);
  transform-origin: bottom;
  background-color: #00f;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  width: 3%;
  height: 25%;
  animation: 60s linear rotate;
  position: absolute;
  top: 25%;
}

.stop-watch__5-sec {
  z-index: 2;
  transform-origin: bottom;
  background-color: green;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  width: 1%;
  height: 50%;
  animation: 5s linear 12 rotate;
  position: absolute;
  bottom: 50%;
}

.stop-watch__center {
  z-index: 3;
  background-color: orange;
  border-radius: 50%;
  width: 7%;
  height: 7%;
  position: absolute;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

